<template>
    <div class="extuserlist-pasenge">
        <div ref="betterscroll" class="use-list-scroller">
            <div class="heightCav">
                <div class="megoods-title-div" v-for="client in clientsdata">
                    <table style="width:100%;height:100%;">
                        <tbody>
                            <tr>
                                <td style="width:90px;height:104px;">
                                    <div style="height:104px;">
                                        <img v-bind:src='client.headimgurl' style="width:70px;height:70px;border-radius:50%;margin:10px;margin-top:15px;"/>
                                    </div>
                                </td>
                                <td>
                                    <div style="position:relative;top:-50px;">
                                        <div>
                                            <span class="goodsname">{{client.username}}</span>
                                            <span style="font-size:12px;color:#c1c1c1">&nbsp;&nbsp;|&nbsp;{{client.conndate|formateTime}}&nbsp;</span>
                                        </div>
                                        <div class="megoods-msg">
                                            <a v-bind:href="client.phone|phoneCall" v-bind:hidden="client.phone|phoneShow">{{client.phone|phoneStr}}</a>
                                            <a class="icon-phone iconfont" v-bind:href="client.phone|phoneCall"></a>
                                        </div>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <empty-bg :msg="emptyMsg" v-show="showEmpty"></empty-bg>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import vImg from "../lazyloadimg/lazyimg.vue";
import BScroll from "better-scroll";
import EmptyBg from "../popupBox/emptyBg.vue";
export default {
  name: "extuser",
  data() {
    return {
      showEmpty: false,
      emptyMsg: "~",
      clientsdata: []
    };
  },
  components: {
    vImg,
    EmptyBg
  },
  activated() {
    // 
    var urlstr = this.path + "php/ttl/userFunction.php";
    this.$http
      .post(urlstr, {
        userid: this.userid,
        type: "extuser"
      })
      .then(response => {
        this.clientsdata = response.body.arr;
        if (this.clientsdata.length > 0) {
          this.showEmpty = false;
        } else {
          this.showEmpty = true;
        }
        this.$nextTick(() => {
          if (!this.betterscroll) {
            this.betterscroll = new BScroll(this.$refs.betterscroll, {
              click: true,
              bounceTime: 900
            });
          } else {
            this.betterscroll.scrollTo(0, 0, 0);
            //
            this.betterscroll.refresh();
          }
        });
      })
      .catch(response=>{
        //
        console.log(response);
      });
  },
  computed: {
    ...mapState(["userid", "path"])
  },
  filters: {
    formateTime: function(val) {
      var date = new Date(val * 1000);
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      d = d < 10 ? "0" + d : d;
      var h = date.getHours();
      h = h < 10 ? "0" + h : h;
      var minute = date.getMinutes();
      var second = date.getSeconds();
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;
      return y + "-" + m + "-" + d;
    },
    phoneStr: function(val) {
      if (val == null || val == "") {
        return "";
      } else {
        return val;
      }
    },
    phoneCall: function(val) {
      if (val == null || val == "") {
        return "#";
      } else {
        return "tel:" + val;
      }
    },
    phoneShow: function(val) {
      if (val == null || val == "") {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {}
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.extuserlist-pasenge {
  position: relative;
  height: 100%;

  .use-list-scroller {
    position: fixed;
    top: 0.3rem;
    left: 0;
    right: 0;
    bottom: 0px;
    overflow: hidden;
  }

  .heightCav {
    box-sizing: border-box;
    padding-bottom: 12px;
  }

  .megoods-img-circle {
    border-radius: 50%;
    width: 70px;
    height: 70px;
    padding: 12px;
    padding-top: 16px;
  }

  .megoods-title-div {
    background-color: #ffffff;
    width: 100%;
    height: 104px;
    margin: 0px;
    padding: 0px;
    margin-bottom: 10px;
  }

  .goodsname {
    font-weight: 600;
    font-size: 18px;
  }

  .megoods-msg {
    padding-top: 12px;
    padding-left: 0px;
    font-size: 14px;
    color: #7a7a7a;
  }

  .iconfont {
    font-size: 18px;
    font-weight: 300;
    margin-right: 4px;
    margin-top: 1px;
    color: #7a7a7a;
  }
}
</style>



